import { Component, OnInit } from '@angular/core';
import { GaTicketingService } from 'src/app/sdk/asset/services';
import { NgxSpinnerService } from 'ngx-spinner';
import { NzMessageService } from 'ng-zorro-antd';
import { Ticketing } from 'src/app/sdk/asset/models';
import { ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-list',
  templateUrl: './list.component.html',
  styleUrls: ['./list.component.scss']
})
export class ListComponent implements OnInit {

    constructor(
        private gaTicketingService: GaTicketingService,
        private spinner: NgxSpinnerService,
        private activatedRoute: ActivatedRoute,
        private nzMessageService: NzMessageService,
    ) {}

    pageIndex = 1
    pageSize = 10
    total = 1
    listOfData: any[] = []
    loading = true
    sortValue: string | null = null
    sortKey: string | null = null
    search: string = ''

    jenis: string = ''
    status: string = ''
    type_request: string = ''
    ngOnInit() {
        this.activatedRoute.paramMap.subscribe(
            data => {
                if (data.get('jenis')) {
                    this.jenis = data.get('jenis');
                }
                if (data.get('status')) {
                    this.status = data.get('status');
                }
                this.searchData()
            }
        )

    }
    sort(sort: { key: string; value: string }): void {
        this.sortKey = sort.key
        this.sortValue = sort.value
        this.searchData()
    }

    searchData(reset: boolean = false): void {
        if (reset) {
            this.pageIndex = 1
        }
        this.loading = true
        this.gaTicketingService
            .getGaTicketings(
                JSON.stringify({
                    pageIndex: this.pageIndex,
                    pageSize: this.pageSize,
                    sortKey: this.sortKey,
                    sortValue: this.sortValue,
                    search: this.search,
                    jenis: this.jenis,
                    status: this.status,
                    type_request: this.type_request,
                }),
            )
            .subscribe((data: any) => {
                this.loading = false
                this.total = data.data.total
                this.listOfData = data.data.data
                console.log(data.data)
            })
    }

    updateFilter(value: string): void {
        this.search = value
        this.searchData(true)
    }

    delete(id) {
        this.spinner.show()
        this.gaTicketingService.deleteGaTicketingsId(id).subscribe(data => {
            this.searchData()
            this.spinner.hide()
        })
    }

    batalDelete() {
        this.nzMessageService.info('click cancel')
    }

}
