import { Component, OnInit } from '@angular/core';
import { GaTicketingService, AssetAssetService, AssetModelxService, AssetStatusService, AssetPartIdService } from 'src/app/sdk/asset/services';
import { VendorVendorService, VendorManufactureService, ToolsLogFlowService } from 'src/app/sdk/user/services';
import { DirektoriPegawaiService, CompanyKantorService, CompanyDepartemenService } from 'src/app/sdk/hr/services';
import { NgxSpinnerService } from 'ngx-spinner';
import { NzNotificationService } from 'ng-zorro-antd';
import { PesanService } from 'src/app/services/tool/pesan.service';
import { Router, ActivatedRoute } from '@angular/router';
import { StandarApprovalService, standarFlow } from 'src/app/components/approval/standar/standar-approval.service';
import { Ticketing, Modelx, Status, Asset, Part_id, Life_limit, Condition_monitoring } from 'src/app/sdk/asset/models';
import { Pegawai_master, Kantor, Departemen } from 'src/app/sdk/hr/models';
import { Vendor, Manufacture } from 'src/app/sdk/user/models';


@Component({
  selector: 'app-detail',
  templateUrl: './detail.component.html',
  styleUrls: ['./detail.component.scss']
})
export class DetailComponent implements OnInit {

    constructor(
        private gaTicketingService: GaTicketingService,
        private vendorVendorService: VendorVendorService,
        private assetAssetService: AssetAssetService,
        private assetModelxService: AssetModelxService,
        private assetStatusService: AssetStatusService,
        private assetPartIdService: AssetPartIdService,
        private vendorManufactureService: VendorManufactureService,
        private direktoriPegawaiService: DirektoriPegawaiService,
        private companyKantorService: CompanyKantorService,
        private companyDepartemenService: CompanyDepartemenService,
        private spinner: NgxSpinnerService,
        private notification: NzNotificationService,
        private pesan: PesanService,
        private router: Router,
        private activatedRoute: ActivatedRoute,
        private toolsLogFlowService: ToolsLogFlowService,
        private standarApprovalService: StandarApprovalService,
    ) { }

    dataTicket: Ticketing = {}
    id_ticketing: string = ''
    id_pegawai: string = ''

    dataPegawai: Pegawai_master = {}
    dataKantor: Kantor = {}
    dataDepartemen: Departemen = {}
    dataModel: Modelx = {}
    dataStatus: Status = {}
    dataAsset: Asset = {}
    dataType: Part_id = {}
    dataVendor: Vendor = {}
    dataManufacture: Manufacture = {}
    dataLifelimit: Life_limit = {};
    dataCondition: Condition_monitoring = {};


    ngOnInit() {
         this.activatedRoute.paramMap.subscribe(
            params => {
                if (params.get('id')) {
                    this.id_ticketing = params.get('id')
                }
                this.getDetailTicket()
                this.getLog()
            }
        )

        this.standarApprovalService.isreload.next(false);
        this.standarApprovalService.getisreload().subscribe(
            data => {
                if (data) {
                    this.getDetailTicket()
                    this.getLog()
                }
            }
        )
    }
    params: standarFlow = null;
    id_data_flow: string = ''

    getDetailTicket() {
        this.spinner.show()
        this.gaTicketingService.getGaTicketingsId(this.id_ticketing).subscribe(
            data => {
                this.dataTicket = data.data;
                this.getPegawai(data.data.id_pegawai);
                this.getkantor(data.data.id_kantor);
                this.getDepartemen(data.data.departemen_tujuan);
                this.getAsset(data.data.id_asset);

                this.params = {
                    id: data.data.id_ticketing,
                    id_data_flow_detail: data.data.id_data_flow_detail,
                    id_data_flow: data.data.id_data_flow,
                    next_status: null,
                    primary_key: "id_ticketing"
                }
                this.standarApprovalService.param.next(this.params)
                this.spinner.hide()
            }
        )
    }

    getPegawai(id_pegawai) {
        this.direktoriPegawaiService.getDirektoriPegawaisId(id_pegawai).subscribe(
            data => {
                this.dataPegawai = data.data;
            }
        )
    }
    getkantor(id_kantor) {
        this.companyKantorService.getCompanyKantorsId(id_kantor).subscribe(
            data => {
                this.dataKantor = data.data;
            }
        )
    }
    getDepartemen(departemen_tujuan) {
        this.companyDepartemenService.getCompanyDepartemensId(departemen_tujuan).subscribe(
            data => {
                this.dataDepartemen = data.data;
            }
        )
    }

    getAsset(id_asset) {
        this.assetAssetService.getAssetAssetsId(id_asset).subscribe(
            data => {
                this.dataAsset = data.data;
                this.getModel(this.dataAsset.id_model)
                this.getStatus(this.dataAsset.id_status)
                this.getVendor(this.dataAsset.id_vendor)

            }
        )
    }


    getModel(id_model) {
        this.assetModelxService.getAssetModelxesId(id_model).subscribe(
            data => {
                this.dataModel = data.data
                this.getPartId(this.dataModel.id_part_id)
                this.getManufacture(this.dataModel.id_manufacture)
            }
        )
    }
    getPartId(id_part_id) {
        this.assetPartIdService.getAssetPartIdsId(id_part_id).subscribe(
            data => {
                this.dataType = data.data
            }
        )
    }
    getManufacture(id_manufacture) {
        this.vendorManufactureService.getVendorManufacturesId(id_manufacture).subscribe(
            data => {
                this.dataManufacture = data.data
            }
        )
    }

    getStatus(id_status) {
        this.assetStatusService.getAssetStatusesId(id_status).subscribe(
            data => {
                this.dataStatus = data.data
            }
        )
    }
    getVendor(id_vendor) {
        this.vendorVendorService.getVendorVendorsId(id_vendor).subscribe(
            data => {
                this.dataVendor = data.data
            }
        )
    }

    listLog = [];
    getLog() {
        this.spinner.show()
        this.toolsLogFlowService.getToolsLogFlows(JSON.stringify({ for_module_id: this.id_ticketing })).subscribe(
            data => {
                this.listLog = data.data;
                this.spinner.hide()
            }
        )
    }



    goToBack() {
        this.router.navigate(['/helpdesk/complaints/list/all/all'])
    }

}
