import { Component, OnInit } from '@angular/core';
import { GaTicketingService, AssetAssetService } from 'src/app/sdk/asset/services';
import { CompanyDepartemenService, CompanyKantorService, DirektoriPegawaiMasterService } from 'src/app/sdk/hr/services';
import { NgxSpinnerService } from 'ngx-spinner';
import { NzNotificationService, NzDrawerService } from 'ng-zorro-antd';
import { FormBuilderTypeSafe, FormGroupTypeSafe } from 'src/app/services/angular-reactive-forms-helper';
import { PesanService } from 'src/app/services/tool/pesan.service';
import { Router, ActivatedRoute } from '@angular/router';
import { Ticketing } from 'src/app/sdk/asset/models';
import { Pegawai_master, Kantor, Departemen } from 'src/app/sdk/hr/models';
import { FormControl } from '@angular/forms';
import { SearchAssetComponent } from 'src/app/components/search-item/search-asset/search-asset.component';

@Component({
    selector: 'app-edit',
    templateUrl: './edit.component.html',
    styleUrls: ['./edit.component.scss']
})
export class EditComponent implements OnInit {

    constructor(
        private gaTicketingService: GaTicketingService,
        private assetAssetService: AssetAssetService,
        private companyDepartemenService: CompanyDepartemenService,
        private companyKantorService: CompanyKantorService,
        private direktoriPegawaiMasterService: DirektoriPegawaiMasterService,
        private spinner: NgxSpinnerService,
        private notification: NzNotificationService,
        private fb: FormBuilderTypeSafe,
        private pesan: PesanService,
        private router: Router,
        private activatedRoute: ActivatedRoute,
        private drawerService: NzDrawerService
    ) { }

    dataTicket: Ticketing = {}

    toogleAsset = false

    listPegawai: Pegawai_master[] = []
    listKantor: Kantor[] = []
    listDepartemen: Departemen[] = []
    id_pegawai: string = ''
    id_ticketing: string = ''
    ngOnInit() {
        this.id_ticketing = this.activatedRoute.snapshot.paramMap.get('id')
        this.getPreeData()
        this.getData()
        this.form()

    }

    toogleShow() {
        this.toogleAsset = false
        if (this.dataForm.value.type_request == 'Asset') {
            this.toogleAsset = true
        }
    }

    getPreeData() {
        this.direktoriPegawaiMasterService.getDirektoriPegawaiMasters().subscribe(data => {
            this.listPegawai = data.data
        })
        this.companyKantorService.getCompanyKantors().subscribe(data => {
            this.listKantor = data.data
        })

        this.companyDepartemenService.getCompanyDepartemens().subscribe(data => {
            this.listDepartemen = data.data
        })
    }

    getData() {
        this.gaTicketingService.getGaTicketingsId(this.id_ticketing).subscribe(
            data => {
                this.dataForm.setValue(data.data)
            }
        )
    }

    dataForm: FormGroupTypeSafe<Ticketing>
    form() {
        this.dataForm = this.fb.group<Ticketing>({
            id_pegawai: new FormControl(''),
            id_kantor: new FormControl(''),
            judul: new FormControl(''),
            type_request: new FormControl(''),
            id_asset: new FormControl(''),
            urgency: new FormControl(''),
            priority: new FormControl(''),
            tanggal_diajukan: new FormControl(''),
            departemen_tujuan: new FormControl(''),
            keterangan: new FormControl(''),
            id_user: new FormControl(''),
            id_ticketing: new FormControl(''),
            created_at: new FormControl(''),
            updated_at: new FormControl(''),
            tanggal_close: new FormControl(''),
            status: new FormControl(''),
            nomor: new FormControl(''),
            id_data_flow: new FormControl(''),
            id_data_flow_detail: new FormControl(''),
            posisi: new FormControl(''),
            jenis: new FormControl(''),
        })
    }

    id_asset: string = ''
    selectAsset() {
        const drawerRef = this.drawerService.create<SearchAssetComponent, { value: string }, string>({
            nzTitle: 'Search Asset',
            nzContent: SearchAssetComponent,
            nzWidth: '900px',
            nzContentParams: {
                value: 'arifan'
            }
        });

        drawerRef.afterOpen.subscribe(() => {
            console.log('Drawer(Component) open');
        });

        drawerRef.afterClose.subscribe(data => {

            if (typeof data === 'string') {
                this.id_asset = data;
                this.dataForm.patchValue({ id_asset: this.id_asset })
                this.beforeAsset()
            }
        });
    }



    dataItem: any = {};

    beforeAsset() {
        console.log("panggie ko")
        this.assetAssetService.getAssetAssetsId(this.id_asset).subscribe(
            data => {
                this.dataItem.sn = data.data.sn;
                this.dataItem.id_asset = data.data.id_asset;
                console.log(this.dataItem)
            }
        )
    }

    simpan() {
        this.spinner.show()
        if (this.dataForm.invalid) {
            this.pesan.pesanWarningForm('Form Tidak Valid')
            this.spinner.hide()
            return
        }

        this.dataTicket = this.dataForm.value
        this.gaTicketingService.putGaTicketingsId({ id: this.id_ticketing, body: this.dataTicket }).subscribe(
            data => {
                this.goToBack()
                this.spinner.hide()
            },
            err => {
                this.pesan.pesanWarningForm('Gagal di simpan')
                this.spinner.hide()
            },
            () => {
                console.log('HTTP request completed.')
            },
        )
    }

    goToBack() {
        this.router.navigate(['/helpdesk/complaints/list/all/all'])
    }

}
